import React, { Component } from 'react';
import axios from 'axios';
import Status from './Status';
import Refresh from './Refresh';
import './SummaryBoard'
import './SummaryBoard.css'

class SummaryBoard extends Component {
    constructor(props) {
        super(props)
        this.state = {
            isLoading: true,
            projects: [],
        }

        this.getSummary = this.getSummary.bind(this)
        this.start = this.start.bind(this)
        this.stop = this.stop.bind(this)
        this.update = this.update.bind(this)
        if (process.env.NODE_ENV && process.env.NODE_ENV === 'development') {
            this.basicUrl = 'http://192.168.40.147:3001/dropship/api/v1';
        } else {
            this.basicUrl = window.location.protocol + '//'+window.location.hostname+':3001/dropship/api/v1';
        }
        
    }

    getSummary = async () => {
        const {
            data: {projects},
        } = await axios.get(this.basicUrl + '/summary');
        
        this.setState({projects, isLoading: false});
    };

    componentDidMount() {
        this.getSummary();
    }

    start = async (project) => {
        await axios.post(this.basicUrl+"/"+project+"/start").then(()=>this.getSummary());
    }

    stop = async (project) => {
        await axios.post(this.basicUrl+"/"+project+"/stop").then(()=>this.getSummary());
    }

    update = async (project) => {
        await axios.post(this.basicUrl+"/"+project+"/update").then(()=>this.getSummary());
    }

    

    render() {
        const {projects, isLoading} = this.state

        return (
            <section className="container">
                <Refresh refresh={this.getSummary} />
                {
                    isLoading? 
                        (<div className="loader"><span className="loader_text">'Loading...'</span></div>) 
                        : 
                        (<div className="items">
                            {projects.sort((p1,p2)=>p1.project.localeCompare(p2.project)).map((project)=> 
                                (
                                    <Status 
                                        key={project.project} 
                                        project={project.project} 
                                        status={project.status} 
                                        uptime={project.uptime} 
                                        pid={project.pid} 
                                        startF={this.start}
                                        stopF={this.stop}
                                        updateF={this.update}
                                    />
                                )
                            )}
                        </div>)
                }
            </section>
        );
    }
}

export default SummaryBoard;