import React from 'react';
import PropTypes from 'prop-types';
import './SummaryBoard.css'

function Status({project, status, pid, uptime, startF, stopF, updateF}) {
    return ( 
        <div className='status'>
            <span className='item'>{project}</span>
            <span className='item'>{status}</span>
            {
                status === "Down" ? 
                <>
                <span className='item'>-</span>
                <span className='item'>-</span>
                <button className='item' onClick={()=>startF(project)}>start</button>
                </> :
                <> 
                <span className='item'>{pid}</span>
                <span className='item'>{uptime}</span>
                
                <button className='item' onClick={()=>stopF(project)}>stop</button>
                </>
            }
            <button className='item' onClick={()=>updateF(project)}>update</button>
        </div>
        );
}

Status.prototype = {
    project: PropTypes.string.isRequired,
    status: PropTypes.string.isRequired,
    uptime: PropTypes.string,
    pid: PropTypes.number,
    startF: PropTypes.func.isRequired,
    stopF: PropTypes.func.isRequired,
    updateF: PropTypes.func.isRequired,
}


export default Status;